body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.panel {
  background-color: black;
  position: absolute;
  right: 48px;
  bottom: 48px;
  width: 300px;
  height: 60px;
  opacity: 0.5;
}

.panel-client-area {
  margin: 24px;
}
.icon {
  color: #FFF;
  width: 36px;
  height: 36px;
  border-radius: 18px;
  line-height: 36px;
  text-align: center;
  font-size: 19px;
  font-weight: 800;
}
.name {
  color: #FFF;
  margin-left: -8px;
  font-size: 18px;
}

.middle-text {
  display: block;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.panel-content {
  position: absolute;
  bottom: 60px;
  right: 72px;
}

.icon-wrap {
  margin-right: 8px;
}
